.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: auto;
}


.header{
  background-color: #282c34;
  height: 30px;
  display: flex;
  align-items: center;

}
.nav-header {
  font-size: calc(10px + 2vmin);
  color: white;
  position: absolute;
  width: 50%;
  height: 50px;
  vertical-align: middle;
  left: 25%;
  right: 25%;
  background: #ecf0f600;
  border-radius: 0px;
  }

.App-link {
  color: #007495;
}

.nav-bar {
  margin-top: 15px;
  margin-left: 21vw;
  width: 35vw;
  display: flex;
  justify-content: space-evenly;
  right: 1px;
}

.nav-item{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: calc(2px + 1vmin);
  line-height: 20px;
}

.search-box{
  box-sizing: border-box;
  position: absolute;
  width: 18vw;
  height: 15px;
  left: 10px;
  top: 15px;
  background: #FFFFFF;
  border: 1px solid #E1E4E6;
  border-radius: 8px;
}

.search-img{
  position: absolute;
  margin-top: 1px;
  left: 2px;
  width: calc(10px + 1vmin);
}

.main{
  height: 100vh;
  width: 70vw;
  display: flex;
  margin-left: 15%;
  margin-right: 15%;
}

.main-headline{
  position: absolute;
  text-align: start;
  margin-left: 15%;
  left: 0px;
  font-family: 'Inter';
  font-style: normal;
  font-size:3.6vw;
  color: #000000;
  border-bottom: 1px solid #c2c2c2;
}
.status-bar{
  position: absolute;
  width: 70%;
  font-size: calc(3px + 1vmin);
  border-bottom: 1px solid #c2c2c2;
  color: #5a5a5a;
}

.status-box{
  float: left;
}
.status-img,
.status-img-imp{
  padding-left: 5px;
  padding-right: 5px;
  vertical-align:middle;
  
}
.status-img-imp{
  margin-left: 10px;
}
.dropdown select{
  float: right;
  font-size: calc(3px + 1vmin);
  margin-right: 5px;
  margin-bottom: 2px;
  width:15vw;;
 vertical-align: middle;
}
.top-menu{
  position: absolute;
  width: 70%;
  margin-left: 15%;
  height: 50px;
  left: 0px;
}
.top-menu ul{
  overflow: hidden;
  position: absolute;
  padding: 0px;
  color: #5a5a5a;
}
.top-menu li{
    display: inline-block;
    position: relative;
    border-radius: 20px;
    padding: 2px 5px;
    margin-right: 12px;
    list-style: none;
    border: 1px solid rgba(191, 191, 191, 0.155);
    font-family: arial;
    width: auto;
    list-style: none;
}
.top-menu li:hover{
  background-color: rgb(196, 244, 228);
}
.navigation{
  position: relative;
  text-align: start;
  color: #808080;
}

.main-content{
  width: 100%;
  height: 1000px;
}
.content-item,
.content-item-CDK
{
  background-color: #00759500;
  display: flex;
  position: relative;
}
.headliner{
  position: relative;
  z-index: 1000;
  background: #FF7724;
  color: #FFFFFF;
  border-radius: 0px 3px 3px 0px;
}
.headliner img,
.headliner span{
  margin-top: 2px;
  width: 10px;
  vertical-align: middle;
  vertical-align: middle;
}

.section-img{
  position:relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
  top: 10px;
  width: 30%;
  height: 90%;
}
  .img-img{
  position: relative;
  top:20px;
  width: 100px;
  height:50%;
}
.section-index{
    top: 5px;
    position: relative;    
  font-size: calc(5px + 1vmin);
}
.section-indexs{
  position: relative; 
    top: 5px;
    width: 10px;
    border-radius: 100%;
    font-size: calc(4px + 1vmin);
}
.img-name{
  font-size: calc(5px + 1vmin);
  margin-top: 20px;
  color: #808080;
}
.section-context{
  position: relative;
  text-align: start;
  width: 50%;
  font-size: calc(2px + 1vmin);
}
.secondry-context{
  margin-left: 2vw;
}
.showmore{
  appearance: none;
  border: none;
  background-color: transparent;
  color:rgb(0, 153, 255) ;
}
.SM img{
  vertical-align: middle;
  color:rgb(0, 153, 255) ;
  width: calc(2px + 1vmin);;
}
.section-rating{
  position: relative;
  width :20%;
}
.ratings{
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 0px 0px 5px 5px;
  background-color: rgb(227, 240, 242);
}
.rate,
.rating-img,
.remark,.list-content, .section-checklist{

  position: relative;
}
.view-button,.view-button-cdk{
  background-color: rgb(69, 126, 233);
  border: none;
  position: relative;
  font-size: calc(2px + 1vmin);
  color: #FFFFFF;
  border-radius: 10px 10px 10px 10px;
}
.context-list{
  margin-left: 2vw;
  border-radius: 3px 3px 3px 3px;
  background-color: rgb(255, 245, 235);
}
.list-index{
  border: 1px solid rgba(89, 88, 88, 0.155);
  background-color: #FFFFFF;
}
.cart-headline{
 text-align: start;
}
.offers{
  border: 1px solid rgba(89, 88, 88, 0.155);
  margin-right:20px;
  background-color: #d6d6d6;
  color: rgb(69, 126, 233);
  border-radius: 3px 3px 3px 3px;
  padding: 3px;
  float: left;
}
.cart-item{
  border-radius: 10px 10px 10px 10px;
  border: 1px solid rgb(137, 156, 225);
}
.cart-catalog{
 display: flex;
 justify-content: space-evenly;
}
.cart-button{
  background-color: rgb(69, 126, 233);
  border: none;
  position: relative;
  font-size: calc(2px + 1vmin);
  color: #FFFFFF;
  border-radius: 10px 10px 10px 10px;
}
.current-price{
  
}
.original-price{
color: #c2c2c2;
}
.off-price{
color: brown;
}

.footer{
  background-color: rgb(30, 32, 44);
  color: #FFFFFF;
  display: flex;
  justify-content: space-evenly;
  text-align: start;
}
.promotion-text{
  text-align: start;
  float: left;
}
.sign-up{
  float: right;
  background-color: rgb(69, 126, 233);
  color: #FFFFFF;
  border-radius: 0px 10px 10px 0px;
}
.cart-item-details,
.cart-item-price{
text-align: start;
}
.c-item{
  color: #000000;
}























